<template>
  <aside class="sidebar" :class="{ collapsed }">
    <div class="sidebar-header">
      <div class="logo" v-show="!collapsed">
        <el-icon :size="24"><Rss /></el-icon>
        <span>RSS Reader</span>
      </div>
      <el-button
        :icon="collapsed ? 'Expand' : 'Fold'"
        text
        @click="$emit('toggle')"
      />
    </div>

    <div class="sidebar-actions" v-show="!collapsed">
      <el-button type="primary" @click="$emit('add-feed')">
        <el-icon><Plus /></el-icon>
        <span>添加订阅</span>
      </el-button>
      <el-button @click="handleRefresh" :loading="feedStore.loading">
        <el-icon><Refresh /></el-icon>
      </el-button>
    </div>

    <nav class="sidebar-nav">
      <div class="nav-section">
        <router-link to="/" class="nav-item" active-class="active">
          <el-icon><House /></el-icon>
          <span v-show="!collapsed">全部文章</span>
          <el-badge
            v-if="feedStore.totalUnreadCount > 0 && !collapsed"
            :value="feedStore.totalUnreadCount"
            :max="999"
          />
        </router-link>
      </div>

      <div class="nav-section">
        <div class="section-header" v-show="!collapsed">
          <span>订阅源</span>
        </div>

        <!-- 按分类分组显示订阅源 -->
        <template v-for="[category, categoryFeeds] in feedStore.feedsByCategory" :key="category">
          <div
            class="category-item"
            :class="{ expanded: expandedCategories.has(category) }"
            @click="toggleCategory(category)"
            v-show="!collapsed"
          >
            <el-icon>
              <ArrowRight v-if="!expandedCategories.has(category)" />
              <ArrowDown v-else />
            </el-icon>
            <span>{{ category }}</span>
            <el-badge
              v-if="getCategoryUnreadCount(categoryFeeds) > 0"
              :value="getCategoryUnreadCount(categoryFeeds)"
              :max="999"
            />
          </div>
          <div v-show="expandedCategories.has(category) || collapsed">
            <FeedItem
              v-for="feed in categoryFeeds"
              :key="feed.id"
              :feed="feed"
              :collapsed="collapsed"
              :indent="!collapsed"
            />
          </div>
        </template>
      </div>
    </nav>
  </aside>
</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue'
import { useFeedStore } from '@/stores/feed'
import FeedItem from './FeedItem.vue'
import type { Feed } from '@/types'

defineProps<{
  collapsed: boolean
}>()

defineEmits<{
  (e: 'toggle'): void
  (e: 'add-feed'): void
}>()

const feedStore = useFeedStore()

const expandedCategories = ref(new Set<string>())

// 默认展开所有分类
onMounted(() => {
  feedStore.feedsByCategory.forEach((_, category) => {
    expandedCategories.value.add(category)
  })
})

function getCategoryUnreadCount(feeds: Feed[]) {
  return feeds.reduce((sum, f) => sum + (f.unreadCount || 0), 0)
}

function toggleCategory(category: string) {
  if (expandedCategories.value.has(category)) {
    expandedCategories.value.delete(category)
  } else {
    expandedCategories.value.add(category)
  }
}

async function handleRefresh() {
  await feedStore.refreshAllFeeds()
}
</script>

