<template>
  <router-link
    :to="`/feed/${feed.id}`"
    class="feed-item"
    :class="{ indent, active: isActive }"
    active-class="active"
  >
    <img
      v-if="feed.feedImageUrl && !collapsed"
      :src="feed.feedImageUrl"
      class="feed-icon"
      @error="handleIconError"
    />
    <el-icon v-else-if="!collapsed"><Rss /></el-icon>
    <span v-show="!collapsed" class="feed-title">{{ feed.feedName }}</span>
    <el-badge
      v-if="feed.unreadCount > 0 && !collapsed"
      :value="feed.unreadCount"
      :max="99"
    />
    <el-tooltip v-if="collapsed" :content="feed.feedName" placement="right">
      <img
        v-if="feed.feedImageUrl"
        :src="feed.feedImageUrl"
        class="feed-icon-collapsed"
        @error="handleIconError"
      />
      <el-icon v-else><Rss /></el-icon>
    </el-tooltip>
  </router-link>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import { useRoute } from 'vue-router'
import type { Feed } from '@/types'

const props = defineProps<{
  feed: Feed
  collapsed: boolean
  indent?: boolean
}>()

const route = useRoute()

const isActive = computed(() => {
  return route.name === 'Feed' && Number(route.params.id) === props.feed.id
})

function handleIconError(e: Event) {
  const img = e.target as HTMLImageElement
  img.style.display = 'none'
}
</script>

<style lang="scss" scoped>
.feed-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 8px 12px;
  border-radius: $border-radius-base;
  color: var(--text-secondary);
  text-decoration: none;
  cursor: pointer;
  transition: $transition-base;
  font-size: 14px;

  &.indent {
    padding-left: 32px;
  }

  &:hover {
    background-color: var(--hover-color);
    color: var(--text-primary);
  }

  &.active {
    background-color: var(--hover-color);
    color: $primary-color;
  }
}

.feed-icon,
.feed-icon-collapsed {
  width: 16px;
  height: 16px;
  border-radius: 4px;
  object-fit: contain;
}

.feed-title {
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.el-badge {
  margin-left: auto;
}
</style>
